.slider{background:url("https://micisse.github.io/the-moviedb-reactjs/assets/images/slider-bg.jpg") no-repeat;background-position:50%;text-align:center;background-size:cover;position:relative;padding-top:150px;padding-bottom:50px;display:flex;align-items:center;justify-content:center}.slider .movie-item{margin-right:15px}.slider .movie-item .title-in{margin-bottom:45px}.slider .movie-item .title-in h6 a{font-size:18px}.slider .movie-item:hover .title-in,.slider .movie-item:hover img{transition:all .5s ease-out;-moz-opacity:1;-khtml-opacity:1;-webkit-opacity:1;opacity:1;-ms-filter:progid:DXImageTransform.Microsoft.Alpha(opacity=100);filter:alpha(opacity=100)}.slider .movie-item:hover h6 a{color:#dcf836}.slider .social-link{display:flex;align-items:center;justify-content:flex-end}.slider .social-link p{font-size:14px;color:#fff;font-weight:700;text-transform:uppercase;margin-bottom:0;margin-right:15px}.slider .social-link a i{color:#fff;font-size:16px}.slider .social-link a i:hover{color:#dcf836}@media (max-width:767px){.slider{padding-bottom:0}.slider .slick-multiItemSlider{max-width:300px;padding-top:60}.slider .slick-multiItemSlider .movie-item{margin-left:5px}}.slider .slick-multiItemSlider{padding-bottom:30px}.slider .slick-multiItemSlider .slick-list{margin-top:30px}.slider .slick-multiItemSlider .slick-dots{text-align:right}