.topbar-filter{display:flex;align-items:center;justify-content:space-between;margin-bottom:30px;border-top:1px solid #405266;border-bottom:1px solid #405266}@media (max-width:767px){.topbar-filter{display:flex;flex-direction:column;padding:15px 0}}.topbar-filter label,.topbar-filter select{font-size:14px;color:#abb7c4;font-weight:300;text-transform:none}.topbar-filter select{width:215px;-webkit-appearance:none;-moz-appearance:none;background:url("https://micisse.github.io/the-moviedb-reactjs/assets/images/drop-icon.png") no-repeat right 20px center;border-left:1px solid #405266;border-right:1px solid #405266;border-top:none;border-bottom:none;color:#fff;font-weight:400}@media (max-width:767px){.topbar-filter select{border:1px solid #405266}}.topbar-filter option{background-color:#020d18}.topbar-filter .active,.topbar-filter i:hover{color:#dcf836}.topbar-filter .pagination2{padding-left:100px}@media (max-width:767px){.topbar-filter .pagination2{padding-left:0;margin-top:15px}}.topbar-filter .pagination2 span{margin-right:15px}.topbar-filter .pagination2 button{border:none;background-color:transparent;cursor:pointer;color:#abb7c4}.topbar-filter .pagination2 button,.topbar-filter .pagination2 span{font-size:14px;color:#abb7c4;font-weight:300;text-transform:none}.topbar-filter .pagination2 button{padding-left:5px;padding-right:5px;color:#4280bf}.topbar-filter .pagination2 button.active,.topbar-filter .pagination2 button:hover{color:#dcf836}.topbar-filter .pagination2 button.disabled{cursor:auto;color:#405266;text-decoration:underline}