.top-search{display:flex;align-items:center;justify-content:flex-start;border:4px solid #020d18;border-radius:3px;margin-bottom:40px}.top-search div,.top-search input{height:46px;border:none;background-color:#233a50!important;color:#abb7c4;font-weight:300;text-transform:none}.top-search div{width:20%;border-right:1px solid #020d18;text-transform:uppercase;color:#fff;display:flex;align-items:center;justify-content:center;font-weight:700}.top-search input::-webkit-input-placeholder{color:#abb7c4;font-family:Titillium Web,sans-serif}.top-search input{background:url("https://micisse.github.io/the-moviedb-reactjs/assets/images/topsearch.png") no-repeat right 20px center}