.movie-card-container{margin:auto;margin-top:25px;border-bottom:1px solid hsla(0,0%,100%,.27);width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.movie-card-content{position:relative;width:90%;max-width:300px;margin:auto;overflow:hidden}.movie-card-content-overlay{background:rgba(0,0,0,.7);position:absolute;height:100%;width:100%;left:0;top:0;bottom:0;right:0;opacity:0;transition:all .4s ease-in-out 0s}.movie-card-content:hover .movie-card-content-overlay{opacity:1}.movie-card-stat{width:260px;display:block;margin:auto;margin-bottom:30px}.movie-card-stat .percent-bar{margin-top:4px}.movie-card-stat .thumbs-icons{display:flex;justify-content:flex-end;align-items:center}.movie-card-stat .thumbs-icons i,.movie-card-stat .thumbs-icons span{font-size:13px;color:#fff;font-weight:700;margin-top:5px}.movie-card-stat .percent-bar .dislikes,.movie-card-stat .percent-bar .likes{transition:width .3s ease;height:8px;width:50%}.movie-card-stat .percent-bar .likes{background:#2487ce;float:left}.movie-card-stat .percent-bar .dislikes{background:red;float:right}.movie-card-image{width:100%;height:370px;border-radius:10px}.movie-card-content-details{position:absolute;text-align:center;padding-left:1em;padding-right:1em;width:100%;top:50%;left:50%;opacity:0;transform:translate(-50%,-50%);transition:all .3s ease-in-out 0s}.movie-card-content:hover .movie-card-content-details{top:50%;left:50%;opacity:1}.movie-card-content-details h3{color:#fff;font-weight:500;letter-spacing:.15em;margin-bottom:.5em;text-transform:uppercase}.movie-card-content-details p{color:#fff;font-size:.8em}.movie-card-fadeIn-top{top:20%}.movie-card-categories{color:#fff;margin-top:10px}.movie-card-categories h6{font-size:.8em;text-align:center}.movie-card-container button{text-transform:uppercase;font-weight:700;margin-left:10px;margin-right:10px;border-radius:30px;border-color:none;border:none;width:40px;height:40px;cursor:pointer}button.card-remove-from-trash-btn{width:auto}.movie-card-container button i{font-size:1.7em}.movie-card-container .card-delete-btn{background-color:red;color:#fff}.movie-card-container button.card-dislike-btn{background-color:#ffc107;color:#000}.movie-card-container button.card-dislike-btn:hover{background-color:#ffc107b6}.movie-card-container button.card-like-btn{background-color:#157347}.movie-card-container button.card-like-btn:hover{background-color:#157347aa}.movie-card-container button.card-details-btn{background-color:#000;color:#fff}.movie-card-container button.card-details-btn:hover{background-color:#fff;color:#000}