header{position:absolute;z-index:100;width:100%;background-color:transparent;transition:all .5s ease-out}@media (max-width:991px){header{background-color:#0f2133}}header .navbar-default{margin-bottom:0;padding:30px 0;background-color:transparent;border:none;position:relative;transition:all .5s ease-out}@media (min-width:992px){header .navbar-default{display:flex;align-items:center;justify-content:flex-start}}header .navbar-default .flex-parent{width:100%}@media (min-width:992px){header .navbar-default .flex-parent{display:flex!important;justify-content:flex-start}}header .navbar-default .flex-parent .menu-left{flex-grow:2;margin-left:30px}header .navbar-default .navbar-nav{display:flex;align-items:center;justify-content:flex-start;list-style:none}header .navbar-default .navbar-nav li a{font-size:14px;color:#abb7c4;font-weight:700;text-transform:uppercase;cursor:pointer}header .navbar-default .navbar-nav li a.active,header .navbar-default .navbar-nav li a:hover{color:#dcf836}header .navbar-default .navbar-nav li a i{margin-top:3px}